.footer{
    margin-top: 1rem;
    padding: 1rem;
    background-color: rgb(17, 17, 17);
    bottom: 0;
    position: fixed;
    left: 0;
    width: 100%;
    color: rgb(27, 26, 26);
   
  }
  .container{
      max-width: 1170px;
      margin:auto;
  }
  .row{
      display: flex;
      flex-wrap: wrap;
  }
  ul{
      list-style: none;
  }
  
  *{
      margin:0;
      padding:0;
      box-sizing: border-box;
  }
  
  .footer-col{
    width: 25%;
    padding: 0 15px;
  }
  .footer-col h4{
   font-size: 18px;
   color: #ffffff;
   text-transform: capitalize;
   margin-bottom: 25px;
   font-weight: 500;
   position: relative;
  }
  .footer-col h4::before{
   content: '';
   position: absolute;
   left:0;
   bottom: -10px;
   background-color: #e91e63;
   height: 2px;
   box-sizing: border-box;
   width: 50px;
  }
  .footer-col ul li:not(:last-child){
   margin-bottom: 10px;
  }
  .footer-col ul li a{
   font-size: 16px;
   text-transform: capitalize;
   color: #ffffff;
   text-decoration: none;
   font-weight: 300;
   color: #f3e8e8;
   display: block;
   transition: all 0.3s ease;
  }
  .footer-col ul li a:hover{
   color: #ffffff;
   padding-left: 8px;
  }
  .footer-col .social-links a{
   display: inline-block;
   height: 10px;
   width: 40px;
   background-color: rgba(255,255,255,0.2);
   margin:0 10px 10px 0;
   text-align: center;
   line-height: 40px;
   border-radius: 50%;
   color: #ffffff;
   transition: all 0.5s ease;
  }
  .footer-col .social-links a:hover{
   color: #24262b;
   background-color: #ffffff;
  }